import { useFormik } from "formik";
import { useState, useEffect, useContext } from "react"
import AuthContext from "../../../Context/AuthContext"
import User from "../../../API/api"
import "./CharacterBuilder.css"
import "../../../GeneralComponents/Forms.css"
import "../../../GeneralComponents/TextStyles.css"
import "../../../GeneralComponents/Buttons.css"
import ConfirmationCheck from "../../../GeneralComponents/ConfirmationCheck";

export default function CharacterBaseStats({ char_id, baseStats }) {
    const { currentUser, token } = useContext(AuthContext)
    const [hasSaveData, setHasSaveData] = useState(false);

    const [confirmationVisible, setConfirmationVisible] = useState(false);

    // controls visibility of the confirmation check mark
    const showConfimrationCheckMark = () => {
        setConfirmationVisible(true);

        setTimeout(() => {
            setConfirmationVisible(false);
        }, 2000);
    }


    useEffect(() => {
        async function getUserInfo() {
            if (Object.keys(baseStats).length > 0) {
                formik.setValues({
                    strength: baseStats.strength,
                    dexterity: baseStats.dexterity,
                    constitution: baseStats.constitution,
                    intelligence: baseStats.intelligence,
                    wisdom: baseStats.wisdom,
                    charisma: baseStats.wisdom
                })
                setHasSaveData(true)
            }

        }
        getUserInfo()
    }, [baseStats])

    /** formik for input validation

    Note that we have to initialize ALL of fields with values. 
    These could come from props, but since we dont want to prefill this form, just use an empty string. If we dont do this, React will yell at us.
    We also pass in validate function with validators and what to do on form submit
    */
    const formik = useFormik({
        initialValues: {
            strength: '',
            dexterity: '',
            constitution: '',
            intelligence: '',
            wisdom: '',
            charisma: ''
        },
        onSubmit: async (values) => {
            const { strength, dexterity, constitution, intelligence, wisdom, charisma } = values;
            User.token = token;
            if (hasSaveData) {
                const response = User.updateCharacterBaseStats(char_id, strength, dexterity, constitution, intelligence, wisdom, charisma, currentUser)
                showConfimrationCheckMark();
                return response;
            } else {
                const response = User.createCharacterBaseStats(char_id, strength, dexterity, constitution, intelligence, wisdom, charisma, currentUser)
                showConfimrationCheckMark();
                setHasSaveData(true)
                return response;
            }
        }
    });

    return (
        <div className="character-section">
            <form
                className="character-base-form"
                onSubmit={formik.handleSubmit}
            >
                <fieldset className="base-stats">
                    <legend className="stats-modifier">0</legend>
                    <div className="form-input-frame">
                        <label
                            className="white-font smaller-font"
                            htmlFor="strength">Str
                        </label>
                        <input
                            type="text"
                            id="strength"
                            name="strength"
                            placeholder="0"
                            className="form-input-shortest"
                            value={formik.values.strength}
                            onChange={formik.handleChange}
                        />
                    </div>
                </fieldset>
                <fieldset className="base-stats">
                    <legend className="stats-modifier">0</legend>
                    <div className="form-input-frame">
                        <label
                            className="white-font smaller-font"
                            htmlFor="dexterity">Dex
                        </label>
                        <input
                            type="text"
                            id="dexterity"
                            name="dexterity"
                            placeholder="0"
                            className="form-input-shortest"
                            value={formik.values.dexterity}
                            onChange={formik.handleChange}
                        />
                    </div>
                </fieldset>
                <fieldset className="base-stats">
                    <legend className="stats-modifier">0</legend>
                    <div className="form-input-frame">
                        <label
                            className="white-font smaller-font"
                            htmlFor="constitution">Con
                        </label>
                        <input
                            type="text"
                            id="constitution"
                            name="constitution"
                            placeholder="0"
                            className="form-input-shortest"
                            value={formik.values.constitution}
                            onChange={formik.handleChange}
                        />
                    </div>
                </fieldset>
                <fieldset className="base-stats">
                    <legend className="stats-modifier">0</legend>
                    <div className="form-input-frame">
                        <label
                            className="white-font smaller-font"
                            htmlFor="intelligence">Int
                        </label>
                        <input
                            type="text"
                            id="intelligence"
                            name="intelligence"
                            placeholder="0"
                            className="form-input-shortest"
                            value={formik.values.intelligence}
                            onChange={formik.handleChange}
                        />
                    </div>
                </fieldset>
                <fieldset className="base-stats">
                    <legend className="stats-modifier">0</legend>
                    <div className="form-input-frame">
                        <label
                            className="white-font smaller-font"
                            htmlFor="wisdom">Wis
                        </label>
                        <input
                            type="text"
                            id="wisdom"
                            name="wisdom"
                            placeholder="0"
                            className="form-input-shortest"
                            value={formik.values.wisdom}
                            onChange={formik.handleChange}
                        />
                    </div>
                </fieldset>
                <fieldset className="base-stats">
                    <legend className="stats-modifier">0</legend>
                    <div className="form-input-frame">
                        <label
                            className="white-font smaller-font"
                            htmlFor="charisma">Cha
                        </label>
                        <input
                            type="text"
                            id="charisma"
                            name="charisma"
                            placeholder="0"
                            className="form-input-shortest"
                            value={formik.values.charisma}
                            onChange={formik.handleChange}
                        />
                    </div>

                </fieldset>




                <button
                    className="action-button"
                    type="submit"
                >
                    {hasSaveData
                        ?
                        "Update"
                        :
                        "Save"
                    }
                </button>
            </form>
            {confirmationVisible && <ConfirmationCheck />}
        </div>
    )
}